@font-face {
  font-family: 'Proxima Nova';
  src: url('../fonts/ProximaNovaRegular.eot');
  src: local('ProximaNovaRegular'), url('../fonts/ProximaNovaRegular.woff') format('woff'), url('../fonts/ProximaNovaRegular.ttf') format('truetype');
}


html {
  position: relative;
  min-height: 100%;
  background-color: #111;
}

body {
  margin: 0;
  overflow-x: hidden;
  overflow-y: visible;
  font-size: 13px;
  font-weight: 500;
  background: #000;
  font-family: 'Proxima Nova', sans-serif;
  color: #aeb4ce;
  cursor: default;
  user-select: none;
}

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  margin: 10px 0;
}

.table .thead-light th {
  font-family: 'Proxima Nova', sans-serif;
}

h1 {
  line-height: 43px;
  font-size: 36px;
}

h2 {
  line-height: 35px;
  font-size: 30px;
}

h3 {
  line-height: 30px;
  font-size: 24px;
}

h4 {
  line-height: 22px;
  font-size: 18px;
}

h5 {
  font-size: 15px;
}

h6 {
  font-size: 13px;
}

p {
  line-height: 1.6;
  font-family: 'Proxima Nova', sans-serif;
  font-size: 12px;
}

* {
  outline: none !important;
}

a {
  font-family: 'Proxima Nova', sans-serif;
  color: #F8F8F8;
}

a:hover, a:active, a:focus {
  outline: 0;
  text-decoration: none;
  color: #bec3d8;
}

.title-text,
.header-title {
  margin-bottom: 8px;
  text-transform: capitalize;
  letter-spacing: 0.02em;
  font-size: 15px;
  font-weight: 500;
  margin-top: 0;
  color: #aeb4ce;
  text-shadow: 0 0 1px rgba(40, 54, 95, 0.1);
}

.page-title-box {
  padding: 25px 0;
}

.page-title-box .page-title {
  font-size: 18px;
  margin: 0;
  color: #aeb4ce;
}

.dropdown-menu.show {
  border: 0;
}

.page-title-box .breadcrumb {
  font-size: 13px;
  margin-bottom: 0;
  padding: 2px 0;
  background-color: transparent;
}

.br-theme-bars-square .br-widget a {
  background-color: #1d1f23;
}

.br-theme-fontawesome-stars-o .br-widget a:after,
.br-theme-css-stars .br-widget a:after,
.br-theme-fontawesome-stars .br-widget a:after {
