/* Dropdown */
.dropdown-menu {
  padding: 4px 0;
  font-size: 13px;
  -webkit-box-shadow: 0 3px 12px rgba(0, 0, 0, 0.05);
  box-shadow: 0 3px 12px rgba(0, 0, 0, 0.05);
  border-color: #1d2a33;
  margin: 0;
}

.dropdown-item.active, .dropdown-item:active {
  color: #f1f5fa;
  text-decoration: none;
  background-color: #f7f7f7;
}

.dropdown-item {
  padding: 6px 1.5rem;
}

