  content: "";
  position: absolute;
  top: 2px;
  left: 7px;
  display: table;
  width: 5px;
  height: 10px;
  border: 1px solid #f1f5fa;
  border-top-width: 0;
  border-left-width: 0;
  -webkit-transform: rotate(45deg);
