/*ICO List countdown*/
.countdown {
  display: flex;
  flex-flow: row nowrap;
  position: relative;
  width: 100%;
  justify-content: center;
  font-size: 1.5rem;
}

.countdown .part {
  position: relative;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  width: 50px;
  padding-bottom: .5rem;
  flex-shrink: 0;
  flex-grow: 0;
  padding-left: 10px;
  padding-right: 10px;
  padding: 4px 10px 10px;
}

.countdown .part::before {
  /*content: ":";*/
  left: -0.25em;
  position: absolute;
  color: #33363c;
}

.countdown .part:nth-child(1)::before,
.countdown .part.no-divider::before {
  display: none;
}

.countdown .part.days::after,
.countdown .part.hours::after,
.countdown .part.minutes::after,
.countdown .part.seconds::after {
  position: absolute;
  content: attr(data-label);
  text-align: center;
  bottom: .25rem;
  left: 0;
  width: 100%;
  font-size: .3em;
  color: inherit;
}

.countdown .part + .part {
  margin-left: .625rem;
}

.countdown .days {
  order: 1;
}

.countdown .hours {
  order: 2;
}

.countdown .minutes {
  order: 3;
}

.countdown .seconds {
  order: 4;
}

.countdown.blink .part::before {
  display: none;
}

