  border-radius: 50%;
  border: 2px solid #222429;
}

@media all and (max-width: 600px) {
  .c-progress-steps li:before {
    top: calc(50% - 8px);
    font-size: 16px;
  }
}

@media all and (min-width: 600px) {
  .c-progress-steps {
    display: table;
    list-style-type: none;
    margin: 20px auto;
    padding: 0;
    table-layout: fixed;
    width: 100%;
  }
  .c-progress-steps li {
    display: table-cell;
    text-align: center;
    padding: 0;
    padding-bottom: 34px;
    white-space: nowrap;
    position: relative;
    border-left-width: 0;
    border-bottom-width: 4px;
    border-bottom-style: solid;
    border-bottom-color: #222429;
  }
  .c-progress-steps li.done {
