  }
  .c-progress-steps li.current:before {
    color: #33363c;
    content: "\f192";
  }
  .c-progress-steps li:before {
    bottom: -15px;
    left: 50%;
    margin-left: -16px;
  }
  .c-progress-steps li span {
    background-color: rgba(80, 93, 130, 0.1);
