.card-auto {
    max-height: 82vh;
    overflow: auto;
}

.pedramWheel, .pedramWheel * {
    /*overflow: visible !important;*/
    box-sizing: border-box;
    -webkit-user-select: none;
    user-select: none;
}

.eWheel-wrapper {
    position: relative;
}

.pedramWheel .eWheel-inner {
    display: block;
    overflow: hidden;
    width: 100%;
    height: 100%;
    position: relative;
}

.pedramWheel .eWheel {
    border-radius: 100%;
    overflow: hidden;
}

.pedramWheel .eWheel, .pedramWheel .eWheel > .eWheel-bg-layer, .pedramWheel .eWheel > .eWheel-txt-wrap, .pedramWheel .eWheel > .eWheel-txt-wrap > .eWheel-txt {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.pedramWheel .eWheel > .eWheel-txt-wrap > .eWheel-txt, .pedramWheel .eWheel > .eWheel-bg-layer {
    margin: 0 auto;
    border-radius: 100%;
    padding: 0;
    list-style: none;
    overflow: hidden;
    color: #ecf0f1;
}

.pedramWheel .eWheel > .eWheel-txt-wrap, .pedramWheel .eWheel > .eWheel-bg-layer {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
}

.pedramWheel .eWheel .eWheel-child .eWheel-inside {
    display: table;
    -webkit-transform: rotate(0) skew(-45deg);
            transform: rotate(0) skew(-45deg);
    width: 50%;
    height: 50%;
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
    text-align: right;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    overflow: hidden;
}

.pedramWheel .eWheel .eWheel-child .eWheel-inside > div {
    display: table-cell;
    vertical-align: middle;
    width: 100%;
    height: 100%;
    -webkit-transform: rotate(25deg);
            transform: rotate(25deg);
    -webkit-transform-origin: 115% 25%;
            transform-origin: 115% 25%;
    padding-right: 40px;
    font-size: 18px;
    font-weight: bold;
}

.pedramWheel .eWheel > .eWheel-bg-layer > div {
    overflow: hidden;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
    border: 1px solid transparent;
    background-color: #404040;
}

.pedramWheel .eWheel > .eWheel-bg-layer > div:nth-child(odd) {
    background-color: #616161;
}

.pedramWheel .eWheel > .eWheel-txt-wrap > .eWheel-txt > div {
    position: absolute;
    top: 50%;
    left: 50%;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;    /*overflow: hidden;*/
    line-height: 1.2em;
    max-height: 23.4em;
    text-align: right;
    -webkit-transform-origin: 0 0;
    transform-origin: 0 1px;
    width: 50%;
    padding-right: 6%;
    font-weight: bold;
    font-size: 100%;
    cursor: default;
    color: #fff;
    text-align: right;
}

.pedramWheel .eWheel-center {
    width: 100%;
    height: 100%;
    border-radius: 100%;
    text-align: center;
}

.pedramWheel .eWheel-center > .ew-center-empty, .pedramWheel .eWheel-center > .ew-center-html {
    max-width: 100%;
    position: relative;
    top: 50%;
    left: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateX(-50%) translateY(-50%);
