@-webkit-keyframes fumes {
  50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    background-color: transparent;
  }
  51% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  100% {
    background-color: #f5f5f5;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes fumes {
  50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    background-color: transparent;
  }
  51% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  100% {
